import Vue from 'vue';
import App from './App';
import router from './router';
import firebase from 'firebase';

Vue.config.productionTip = false;

let app;
const config = {
  apiKey: 'AIzaSyB1zrKUHIcK-pUAnDZdk-4JKNFc2Lr1bec',
  authDomain: 'budzet-dev.firebaseapp.com',
  databaseURL: 'https://budzet-dev.firebaseio.com',
  projectId: 'budzet-dev',
  storageBucket: 'budzet-dev.appspot.com',
  messagingSenderId: '227927254747'
};

firebase.initializeApp(config);

firebase.auth().onAuthStateChanged(user => {
  if (!app) {
    /* eslint-disable no-new */
    app = new Vue({
      el: '#app',
      router,
      render: h => h(App)
    });
  }
});



