<template>
  <div class="login">
    <h3>Sign In</h3>
    <input type="text" v-model="email" placeholder="username"><br />
    <input type="password" v-model="password" placeholder="password"><br />
    <button @click="login">Connection</button>
    <p>You don't have an account? <router-link to="/sign-up">Create a new one.</router-link></p>
  </div>
</template>

<script>
  import firebase from 'firebase';

  export default {
    name: 'login',
    data() {
      return {
        email: '',
        password: ''
      };
    },
    methods: {
      login() {
        firebase.auth().signInWithEmailAndPassword(this.email, this.password)
          .then(
            (user) => this.$router.replace('hello'),
            (err) => console.log('Oops', err)
          );
        // this.$router.replace('hello');
      }
    }
  };
</script>

<style scoped>
  .login {
    margin-top: 40px;
  }

  input {
    margin: 10px 0;
    width: 10%;
    padding: 15px;
  }

  button {
    margin-top: 10px;
    width: 10%;
    cursor: pointer;
  }

  p {
    margin-top: 40px;
    font-size: 13px;
  }

  p a {
    text-decoration: underline;
    cursor: pointer;
  }
</style>


